@import "reset";
@import "mixins";
@import "fonts";
@import "vars";
@import "generated/sprite-svg";
@import "libs/customSelect";
@import "libs/accordion";
@import "libs/ddslick";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

html,
body {
  height: 100%;
}

.main {
  flex: 1 0 auto;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  font-family: $font-primary;
  font-size: 14px;
  line-height: 1.6;
}

.container {
  width: 100%;
  max-width: 1370px;
  margin: 0 auto;
  @media only screen and (max-width: 1370px) {
    padding-left: 2%;
    padding-right: 2%;
  }
}

.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  &.align-start {
    align-items: flex-start;
  }
}

.text-center {
  text-align: center;
}

.flex-start {
  align-items: flex-start;
}

a {
  color: inherit;
  transition: all 0.2s ease;
}

button {
  border: none;
}

.paragraph {
  margin-bottom: 2em;
  @include rfs(18);
  line-height: 1.35;
}

.text-bold {
  font-family: $font-bold;
}

// header begin
.header {
  border: solid 1px #c2c2c2;
  .row {
    min-height: 100px;
  }
  // background-color: #f8f8f8;
  &__logo img {
    max-width: 170px;
  }
  &__right {
    flex-grow: 1;
    margin-left: 65px;
  }
  &__phone {
    margin-right: 25px;
    font-size: 16px;
    &:hover {
      color: $ocean-blue;
    }
  }
  &__login {
    color: $snot-green;
    &:hover {
      color: darken($snot-green, 5%);
    }
  }
}

.nav {
  &__item {
    margin-right: 27px;
    &:hover {
      color: $ocean-blue;
    }
  }
}

// form styles begin
.form {
  .col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .form__label {
    margin-bottom: 5px;
    font-family: $font-semibold;
    font-size: 11px;
    font-weight: 600;
    letter-spacing: 2.2px;
    color: #01263a;
    text-transform: uppercase;
  }
  .form__input {
    height: 60px;
    margin-bottom: 1em;
    padding-left: 10px;
    border: solid 1px #e1e1e1;
    font-size: 16px;
  }
  &__forgot {
    margin-top: 5px;
    position: relative;
    font-family: $font-light;
    font-size: 16px;
    font-weight: 100;
    line-height: 1.5;
    color: $charcoal;
    &:hover {
      color: $ocean-blue;
      &:after {
        background-color: $ocean-blue;
      }
    }
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 5px;
      display: inline-block;
      height: 1px;
      width: 100%;
      background-color: $charcoal;
    }
  }
}

// form styles end
// buttons
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 146px;
  min-width: 100px;
  height: 45px;
  color: #fff;
  font-family: $font-primary;
  @include rfs(18);
  font-weight: 600;
  border-radius: 2px;
  &:hover {
    cursor: pointer;
  }
  &--green {
    background-color: $snot-green;
    &:hover {
      background-color: darken( $snot-green, 3%);
    }
  }
  &--blue {
    background-color: #006eac;
    &:hover {
      background-color: lighten( #006eac, 3%);
    }
  }
  &--empty {
    color: $snot-green;
    background-color: #ffffff;
    border: solid 1px $snot-green;
    &:hover {
      color: #ffffff;
      background-color: darken( $snot-green, 3%);
    }
  }
}

// icons
.footer {
  .icon-marker,
  .icon-mail,
  .icon-phone {
    fill: #949494;
  }
  .icon-marker {
    width: 12px;
    height: 17px;
  }
  .icon-mail {
    width: 18px;
    height: 12px;
  }
  .icon-phone {
    width: 13px;
    height: 20px;
  }
}

.icon-twitter,
.icon-google-plus,
.icon-facebook {
  fill: #828282;
  &:hover {
    fill: darken(#828282, 5%)
  }
}

.icon-facebook {
  font-size: 28px;
}

.icon-twitter {
  font-size: 21px;
}

.icon-google-plus {
  font-size: 23px;
}

// icons end
// footer begin
.footer {
  flex: 0 0 auto;
  padding-top: 20px;
  padding-bottom: 8px;
  font-family: $font-light;
  border-top: 1px solid #e1e1e1;
  .container--1260 {
    max-width: 1260px;
  }
  &__top {
    @media only screen and (max-width: 768px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__logo {
    margin-bottom: 25px;
    img {
      max-width: 231px;
    }
  }
  .footer__right {
    .bnt-book {
      margin-top: 34px;
      margin-left: auto;
      @media only screen and (max-width: 768px) {
        margin: 0 auto;
        margin-top: 10px;
      }
    }
  }
  .socials {
    padding: 12px 0;
    flex-wrap: wrap;
  }
  .socials__link:not(:first-child) {
    margin-left: 30px;
    @media only screen and (max-width: 480px) {
      margin-left: 20px;
    }
  }
  .socials__link--fb {
    margin-left: 18px;
  }
  &__bottom {
    @media only screen and (max-width: 992px) {
      flex-direction: column;
      margin-top: 30px;
      .footer__copy {
        order: 2;
      }
    }
  }
  &__copy {
    padding: 7px 0;
    font-size: 12px;
    color: $charcoal;
  }
  .copy__link {
    &:hover {
      color: $ocean-blue;
    }
  }
  &__help {
    .powered {
      margin-right: 40px;
      color: #878787;
    }
  }
  .help__chat {
    @media only screen and (max-width: 992px) {
      max-width: 200px;
    }
    @media only screen and (max-width: 480px) {
      max-width: 150px;
    }
  }
}

.contact {
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: 16px;
    font-family: $font-light;
    color: $charcoal;
  }
  &__item--tel {
    font-family: $font-semibold;
    color: $charcoal;
  }
  &__icon {
    width: 26px;
    display: flex;
    justify-content: center;
  }
}

// footer end
// content begin 
.content {
  padding: 100px 0;
  @media only screen and (max-width: 992px) {
    padding: 75px 0;
  }
  @media only screen and (max-width: 480px) {
    padding: 50px 0;
  }
  .container {
    max-width: 1100px;
  }
  &__title {
    font-family: $font-bold;
    @include rfs(34);
    font-weight: bold;
    color: #013654;
  }
  &__subtitle {
    @include rfs(26);
    margin-bottom: 1em;
  }
  &__text {
    margin-top: 2.5em;
    margin-bottom: 2em;
    @include rfs(18);
    color: #393939;
  }
  .paragraph__title {
    @include rfs(26);
    margin-bottom: 0.5em;
  }
}

.section__title {
  font-family: $font-bold;
  @include rfs(50);
  font-weight: bold;
  color: #012b43;
}

// content end
.video {
  &__frame {
    @media only screen and (max-width: 768px) {
      width: 100%;
    }
    @media only screen and (max-width: 480px) {
      height: 250px;
    }
  }
}

// page INDEX
// hero begin
.hero {
  padding: 90px 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: center;
  &__row {
    @media only screen and (min-width: 1100px) {
      justify-content: flex-end;
      .hero__heading {
        margin-right: 87px;
      }
      .hero__book {
        margin-right: 22px;
      }
    }
    @media only screen and (max-width: 1100px) {
      .hero__title {
        @include rfs(50.5);
        span {
          @include rfs(61.4);
        }
      }
      .hero__subtitle {
        font-size: 22px;
      }
    }
    @media only screen and (max-width: 992px) {
      flex-direction: column;
      .hero__title {
        text-align: center;
      }
      .hero__subtitle {
        text-align: center;
      }
    }
    @media only screen and (max-width: 768px) {
      .hero__title {
        @include rfs(33);
        span {
          @include rfs(40);
        }
      }
      .hero__subtitle {
        font-size: 16px;
      }
    }
    @media only screen and (max-width: 480px) {
      .hero__title {
        @include rfs(25.5);
        span {
          @include rfs(31);
        }
      }
      .hero__subtitle {
        font-size: 14px;
      }
    }
  }
  &__heading {
    margin-bottom: 50px;
  }
  &__title {
    font-family: $font-extrabold;
    font-weight: 800;
    @include rfs(65.7);
    line-height: 0.9;
    color: #3ecff9;
    text-transform: uppercase;
    text-align: right;
    span {
      display: block;
      @include rfs(79.7);
      line-height: 1;
      color: #aaea04;
    }
  }
  &__subtitle {
    margin-top: 20px;
    font-family: $font-extrabold;
    font-weight: 800;
    @include rfs(29);
    color: #012b43;
    text-align: right;
  }
  &__book {
    width: 300px;
    margin-bottom: 30px;
    box-shadow: 1px 2px 18px -2px rgba(0, 0, 0, 0.3);
  }
  .select_mate {
    border-bottom: solid 1px #e1e1e1;
    &:first-child {
      border-top-left-radius: 3px;
      border-top-right-radius: 3px;
    }
  }
  .btn-submit {
    height: 78px;
    width: 100%;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }
}

// hero end
.about_us-content {
  border-bottom: 1px solid #e1e1e1;
  .container {
    max-width: 1370px;
  }
  .row {
    @media only screen and (max-width: 992px) {
      flex-direction: column;
    }
  }
  .col {
    flex-basis: 50%;
  }
  .paragraph {
    color: #1d1d1d;
    font-size: 16px;
  }
  .underlined {
    margin-left: 50px;
    padding-left: 30px;
    margin-bottom: 30px;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -25px;
      width: 80%;
      max-width: 430px;
      height: 1px;
      background-color: #e1e1e1;
    }
    @media only screen and (max-width: 992px) {
      margin-left: 0;
      padding-left: 0;
      &:after {
        width: 120%;
        bottom: 0;
      }
    }
  }
  .btn {
    margin-top: 40px;
  }
}

.benefits {
  &-content {
    padding-top: 76px;
  }
  &__moto {
    margin: 0 auto;
    @include rfs(26);
    color: #012b43;
  }
}

// page index end
// page ABOUT 
.about-content {
  .content__text {
    color: #a1a1a1;
  }
}

// page CONTACT_US
.contact-content {
  .contact {
    margin-bottom: 100px;
  }
  .contact__item {
    margin-bottom: 1em;
    @include rfs(24);
    font-family: $font-primary;
    color: #393939;
  }
  .contact__icon {
    width: 30px;
    margin-right: 25px;
    .icon {
      fill: #006dac;
    }
  }
  .icon-marker {
    @include rfs(38);
  }
  .icon-mail {
    @include rfs(20);
  }
  .icon-phone {
    @include rfs(26);
  }
  .paragraph__title {
    color: #013654;
  }
}

// page THANKS
.thanks-content {
  text-align: center;
  padding-bottom: 45px;
  .container {
    max-width: 910px;
  }
  .paragraph {
    line-height: 1.67;
  }
  .thanks__form {
    .row {
      justify-content: center;
      flex-wrap: wrap;
    }
    .form__input {
      width: 370px;
      @media only screen and (max-width: 480px) {
        width: 100%;
      }
    }
    .col {
      margin-left: 15px;
      margin-right: 15px;
      @media only screen and (max-width: 480px) {
        width: 100%;
      }
    }
    .btn-submit {
      width: 230px;
      margin-top: 35px;
    }
  }
}

// page PATNERS
.partners-content {
  .content__title {
    text-align: center;
  }
  .content__text {
    margin-top: 30px;
    text-align: center;
    color: #a1a1a1;
  }
  .partners-table {
    margin-top: 100px;
    .container {
      max-width: 953px;
    }
  }
  .partners__item {
    padding: 34px 0;
    border-top: 1px solid #ccc;
    @media only screen and (max-width: 480px) {
      padding: 15px 0;
    }
    &:last-child {
      border-bottom: 1px solid #ccc;
    }
  }
  .partners__brand {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 237px;
    padding-right: 15px;
    min-height: 104px;
    border-right: 4px solid #ebebeb;
    img {
      max-width: 100%;
    }
  }
  .partners__desc {
    flex-grow: 1;
    padding-left: 15px;
    padding-right: 20px;
    font-family: $font-bold;
    font-size: 14px;
    line-height: 1.42857143;
  }
}

// page SERVICES
.services-content {
  .paragraph__title {
    font-family: $font-bold;
    @include rfs(24px);
    color: #595959;
  }
  .paragraph {
    color: #a1a1a1;
    span {
      color: #505050;
    }
  }
  .services {
    margin-top: 70px;
    .container {
      max-width: 1110px;
      padding: 0;
    }
  }
  .services__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .services__item {
    flex-basis: 33.3%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 36px;
    padding-bottom: 15px;
    @media only screen and (min-width: 768px) {
      &:nth-child(-n+3) {
        border-bottom: solid 1px #e1e1e1;
      }
      &:nth-child(2),
      &:nth-child(5) {
        border-left: solid 1px #e1e1e1;
        border-right: solid 1px #e1e1e1;
      }
      .btn {
        margin-top: 10px;
      }
    }
    @media screen and (max-width: 768px) and (min-width: 580px) {
      flex-basis: 50%;
      &:nth-child(-n+4) {
        border-bottom: solid 1px #e1e1e1;
      }
      &:nth-child(1),
      &:nth-child(3),
      &:nth-child(5) {
        border-right: solid 1px #e1e1e1;
      }
    }
    @media only screen and (max-width: 580px) {
      flex-basis: 100%;
      &:nth-child(-n+5) {
        border-bottom: solid 1px #e1e1e1;
      }
    }
  }
  .services__picture {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
    }
  }
  .services__name {
    text-align: center;
    font-family: $font-bold;
    color: #000;
    font-size: 30px;
    font-weight: 700;
    line-height: 0.9;
    margin: 20px 0;
  }
  .services__desc {
    max-width: 90%;
    font-family: $font-light;
    color: #1d1d1d;
  }
}

// page FAQ
.faq-content {
  .content__subtitle {
    @include rfs(24);
    font-family: $font-bold;
    text-align: center;
    color: #a1a1a1;
  }
  .accordion {
    margin-top: 70px;
  }
}

@import "media";